var that;
import order from '../order.js';
export default {
	data() {
		return {
			CustomBar: this.CustomBar,
			TabCur: 0,
			tab: [{ id: '-1', title: '全部订单' }, { id: '0', title: '待付款' }, { id: '1', title: '已付款' }, { id: '2', title: '已完成' }],
			orderlist: [],
			page: 1,
			nomore: false,
			keyword: '',
			status: '',
			onlist: false
		};
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.index;
			this.status = e.currentTarget.dataset.id;
			that.page = 1;
			that.orderlist = [];
			that.nomore = false;
			that.getlist();
		},
		getlist() {
			if (that.nomore) {
				return;
			}
			that.onlist = false;
			this.$request
				.request({
					url: that.api.share.get_order,
					data: {
						status: that.status,
						page: that.page,
						keyword: that.keyword
					},
					Toast: '正在加载...'
				})
				.then(res => {
					console.log('列表:', res);
					if (res.length > 0) {
						var list = res;
						that.orderlist = that.orderlist.concat(list);
						that.page = that.page + 1;
					} else {
						if (that.page != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了!');
								that.nomore = true;
							}
						} else {
							that.onlist = true;
						}
					}
					that.$forceUpdate();
				});
		},
		search(e) {
			that.keyword = e.detail.value;
			that.page = 1;
			that.orderlist = [];
			that.nomore = false;
			that.getlist();
		},
		kinp() {
			that.keyword = '';
			that.page = 1;
			that.orderlist = [];
			that.nomore = false;
			that.getlist();
		},
		toinfo(e) {
			let torderinfo= that.orderlist[e.currentTarget.dataset.index] 
			uni.setStorageSync("torderinfo",torderinfo)
			uni.navigateTo({
				url: '/pages/earnings/orderinfo/orderinfo'
			});
		}
	},
	onLoad() {
		that = this;
		that.getlist();
		// that.orderlist = order.orderlist;
		// console.log(that.orderlist);
	},
	onReachBottom() {
		that.getlist();
	},
	onPullDownRefresh() {
		that.keyword = '';
		that.page = 1;
		that.orderlist = [];
		that.nomore = false;
		that.getlist();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	}
};
